import { FC, useEffect, useState } from "react";
import Sidebar from "../Sidebar/Sidebar";
import { useParams } from "react-router-dom";
import { useAppSelector } from "../../app/hooks";
import { selectPosts } from "../../features/Post/PostSlice";
import { IPost } from "../../interface/post.interface";
import BlogPostItem from "../PostCase/BlogPostItem/BlogPostItem";
import BlogSide from "../PostCase/BlogPostList/BlogSide/BlogSide";

const BlogDetails: FC = () => {
  const { posts } = useAppSelector(selectPosts);
  const { postid } = useParams();
  const [foundpost, setfoundpost] = useState<null | IPost>(null);

  useEffect(() => {
    const filterpost = posts.filter((p) => p.id === postid);
    if (filterpost.length > 0) {
      setfoundpost(filterpost[0]);
    }
  }, [postid, posts]);
  return (
    <div>
      <>
        <Sidebar />
        <div className="container">
          <div className="row">
            <div className="card my-5">
              <div className="card-body h3 ">
                {foundpost && foundpost.title}
              </div>
            </div>

            <div className="row ">
              <div className="col-lg-8">
                <div className="row">
                  <div className="col-lg-12">
                    {foundpost !== null ? (
                      <BlogPostItem {...foundpost} isBlogItem />
                    ) : (
                      <h2>No Post Found</h2>
                    )}
                  </div>
                </div>
              </div>
              <div className="col-lg-4">
                <BlogSide />
              </div>
            </div>
          </div>
        </div>
      </>
    </div>
  );
};

export default BlogDetails;
