import { FC } from "react";
import { Link } from "react-router-dom";
import { ContentType, IPost } from "../../../interface/post.interface";
import moment from "moment";
import styles from "./BlogPostItem.module.css";

const BlogPostItem: FC<IPost & { isBlogItem?: boolean }> = ({
  title,
  content,
  contentType,
  source,
  dateTime,
  catagories,
  id,
  isBlogItem = false,
}) => {
  return (
    <div>
      <div className="blog-post">
        <div className="blog-thumb">
          {contentType === ContentType.Image ? (
            <figure>
              <img
                className={`${styles.image_top} w-100`}
                src={`${source}`}
                alt="img"
              />
            </figure>
          ) : (
            <>
              <iframe
                width="100%"
                height="400"
                allowFullScreen
                src={`${source}`}
                title="YouTube video player"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              />
            </>
          )}

          {/* <img src="assets/images/business_img.jpg" alt="" /> */}
        </div>
        <div className="down-content mx-3">
          <span className="text-capitalize h3 text-warning mx-3">
            {catagories}
          </span>
          {isBlogItem ? (
            <></>
          ) : (
            <Link to={`/posts/${id}`}>
              <h4 className="text-capitalize h3 text-dark mx-3">{title}</h4>
            </Link>
          )}

          <ul className="post-info d-flex justify-content-between">
            <li>Admin</li>
            <li>
              {moment(dateTime.toString()).format("MMMM Do YYYY, h:mm:ss a")}
            </li>
          </ul>
          {isBlogItem ? (
            <p>{content}</p>
          ) : (
            <p>{content.substring(0, 200) + " ...."}</p>
          )}

          <hr />
          {isBlogItem ? (
            <></>
          ) : (
            <div className="mt-3">
              <Link to={`/posts/${id}`}>
                <div className="btn btn-outline-info">More</div>
              </Link>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default BlogPostItem;
