import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import { child, get, ref } from "firebase/database";
import { RootState } from "../../app/store";
import { database } from "../../firebase";
import { ICatagories, IPost } from "../../interface/post.interface";

export type PostState = {
  posts: IPost[];
  status: "loading" | "success" | "error";
  error: String | null;
};

const initialState: PostState = {
  posts: [],
  error: null,
  status: "loading",
};

export const fetchPosts = createAsyncThunk("posts/fetchPost", async () => {
  try {
    const resultposts = await get(child(ref(database), `posts`)).then(
      (snapshot) => {
        const results: IPost[] = [];
        snapshot.forEach((e) => {
          e.forEach((e) => {
            console.log(e.val());

            results.push(e.val());
          });
        });
        return results;
      }
    );

    return resultposts;
  } catch (e) {
    return Promise.reject(`Something went wrong!! ${e}`);
  }
});

const PostSlice = createSlice({
  name: "posts/slice",
  initialState,
  reducers: {
    addPost: (state, action: PayloadAction<IPost>) => {
      state.posts.push(action.payload);
    },
    addPosts: (state, action: PayloadAction<IPost[]>) => {
      state.posts = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder.addCase(fetchPosts.pending, (state) => {
      state.status = "loading";
    });
    builder.addCase(fetchPosts.rejected, (state, action) => {
      state.status = "error";
      state.error = `Somethings went wrong ${action.payload}`;
    });

    builder.addCase(fetchPosts.fulfilled, (state, action) => {
      state.status = "success";
      state.posts = action.payload;
      state.error = null;
    });
  },
});

export const { addPost, addPosts } = PostSlice.actions;

export const selectPosts = (state: RootState) => state.post;
export const selectPostsByCatagories = (
  state: RootState,
  catagorie: ICatagories
) => state.post.posts.filter((post) => post.catagories === catagorie);

export default PostSlice.reducer;
