import { FC, useEffect, useState } from "react";
import { Alert, Spinner } from "react-bootstrap";
import { useParams } from "react-router-dom";
import { useAppSelector } from "../app/hooks";
import PostList from "../component/PostCase/PostList";
import Sidebar from "../component/Sidebar/Sidebar";
import { selectPosts } from "../features/Post/PostSlice";
import { IPost } from "../interface/post.interface";

const CatagoriesPage: FC = () => {
  const { cat } = useParams();
  const { status, posts, error } = useAppSelector(selectPosts);

  const [postsList, setPostsList] = useState<IPost[]>([]);

  useEffect(() => {
    if (cat) {
      setPostsList(posts.filter((post) => post.catagories === cat));
    }
  }, [cat, posts]);

  return (
    <>
      <Sidebar />
      <div className="container">
        <div className="row">
          <div className="card mt-5">
            <div className="card-body h3">{cat}</div>
            <div className="text-muted">
              {postsList && postsList.length} items
            </div>
          </div>
        </div>

        {error ? (
          <>
            <Alert>{error}</Alert>
          </>
        ) : (
          <></>
        )}

        {status === "loading" ? (
          <h2 className="display-3 fw-bold fadeIn text-capitalize">
            <Spinner animation="border"></Spinner>
            "Loading...."
          </h2>
        ) : (
          <PostList posts={postsList} />
        )}
      </div>
    </>
  );
};

export default CatagoriesPage;
